import React, { Fragment, useState } from "react";

const InvestProcessModal = ({ itemProcess, closeModal }) => {
  const [agreeCheck, setAgreeCheck] = useState(false);
  const [uploadReceipt, setUploadReceipt] = useState("");
  const [uploadScan, setUploadScan] = useState("");
  const [receivePerson, setReceivePerson] = useState("");
  const [sendPerson, setSendPerson] = useState("");
  const [moneyTotal, setMoneyTotal] = useState("");
  const [payNumber, setPayNumber] = useState("");
  const [successProcess, setSuccessProcess] = useState(false);

  const onSubmit = () => {
    console.log(
      "a",
      agreeCheck,
      uploadScan,
      uploadReceipt,
      receivePerson,
      sendPerson,
      moneyTotal,
      payNumber
    );
    setSuccessProcess(true);
  };
  const onFinish = () => {
    closeModal()
  }
  return (
    <div className="invest-process">
      {!successProcess ? (
        <Fragment>
          {" "}
          <div className="head">
            <p className="title">
              {itemProcess.investType === 0
                ? "Bn ang x l u t bng hnh thc chuyn khon"
                : "Bn ang x l u t bng hnh thc nhn tin mt"}
            </p>
          </div>
          <div className="content">
            {itemProcess.investType === 0 ? (
              <div className="part">
                <p className="title">
                  Xc nhn  nhn c tin chuyn khon theo cc thng tin
                </p>
                <div className="info-content">
                  <p className="desc">
                    Ch ti khon: <span className="bold">on c Mnh</span>
                  </p>
                  <p className="desc">
                    S ti khon nhn tin u t: 12345689090900
                  </p>
                  <p className="desc">Ngn hng: Vietcombank</p>
                  <p className="desc">Chi nhnh: H Ni</p>
                  <p className="desc">S tin: 1000000</p>
                  <p className="desc">
                    <label
                      htmlFor="checkbox"
                      className={`checkbox__label ${
                        agreeCheck ? "active" : ""
                      }`}
                    >
                      <input
                        type="checkbox"
                        name="agree"
                        id="checkbox"
                        onChange={e => setAgreeCheck(e.target.checked)}
                      />{" "}
                      Xc nhn
                    </label>
                  </p>
                </div>
              </div>
            ) : (
              <div className="part">
                <p className="title">
                  Xc nhn  nhn c tin u t bng tin mt
                </p>
                <div className="form-content">
                  <div className="bs-row row-sm-10">
                    <div className="bs-col sm-50-10">
                      <div className="item">
                        <label htmlFor="">Ngi nhn tin</label>
                        <input
                          type="text"
                          className="modal__input"
                          name="receivePerson"
                          onChange={e => setReceivePerson(e.target.value)}
                        />
                      </div>
                    </div>
                    <div className="bs-col sm-50-10">
                      <div className="item">
                        <label htmlFor="">Ngi np tin</label>
                        <input
                          type="text"
                          className="modal__input"
                          name="sendPerson"
                          onChange={e => setSendPerson(e.target.value)}
                        />
                      </div>
                    </div>
                    <div className="bs-col sm-50-10">
                      <div className="item">
                        <label htmlFor="">S tin</label>
                        <input
                          type="number"
                          className="modal__input"
                          name="moneyTotal"
                          onChange={e => setMoneyTotal(e.target.value)}
                        />
                      </div>
                      <div className="item">
                        <label htmlFor="">S phiu thu</label>
                        <input
                          type="text"
                          className="modal__input"
                          name="payNumber"
                          onChange={e => setPayNumber(e.target.value)}
                        />
                      </div>
                    </div>
                    <div className="bs-col sm-50-10">
                      <div className="item upload-item">
                        <div className="upload__file">
                          <input
                            type="file"
                            className="modal__input"
                            id="upload__img"
                            onChange={e => setUploadReceipt(e.target.value)}
                          />
                          <img
                            src="images/icon_upload_img.gif"
                            alt=""
                            className="img__icon"
                          />
                          <img src="" alt="" className="result__img" />
                        </div>
                        <label htmlFor="">
                          {uploadReceipt === ""
                            ? "Upload nh phiu thu"
                            : uploadReceipt}
                        </label>
                      </div>
                      <div className="item">
                        <label
                          htmlFor="checkbox1"
                          className={`checkbox__label ${
                            agreeCheck ? "active" : ""
                          }`}
                        >
                          <input
                            type="checkbox"
                            name="agree"
                            id="checkbox1"
                            onChange={e => setAgreeCheck(e.target.checked)}
                          />{" "}
                          Xc nhn
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <div className="part">
              <p className="title">
                Xc nhn  hon thnh hp ng k kt 2 bn
              </p>
              <div className="upload__img">
                <input
                  type="file"
                  className="modal__input"
                  id="upload__img"
                  onChange={e => setUploadScan(e.target.value)}
                />
                <img
                  src="images/icon_upload_img.gif"
                  alt=""
                  className="img__icon"
                />
                <img src="" alt="" className="result__img" />
              </div>
              <p className="desc">
                {uploadScan === ""
                  ? "Upload bn scan hp ng  k kt"
                  : uploadScan}
              </p>
            </div>
          </div>
          <div className="bottom">
            <button
              className="bottom__btn green__btn"
              type="button"
              onClick={onSubmit}
            >
              kch hot u t
            </button>
            <button className="bottom__btn red__btn" type="button" onClick={onFinish}>
              hy
            </button>
          </div>
        </Fragment>
      ) : (
        <Fragment>
        <div className="success-content">
        <div className="head">
            <p className="desc">Kch hot thnh cng gi u t</p>
          </div>
          <div className="content">
            <img src="images/icon_success_process.gif" alt="" />
          </div>
          <div className="bottom">
            <button
              className="bottom__btn green__btn"
              type="button"
              onClick={onFinish}
            >
              OK
            </button>
          </div>
        
        </div>
          
        </Fragment>
      )}
    </div>
  );
};

export default InvestProcessModal;
