const TableList = () => {
  const [toggleModal, setToggleModal] = useState(false);
  const [modalType, setModalType] = useState("");
  const showModalType = value => {
    setModalType(value);
    setToggleModal(true);
  };
  return (
      <div className="table-content process-table">
      <div className="table">
        <table>
          <thead>
          <tr>
            <th>M hp ng</th>
            <th>H tn <br/> nh u t</th>
            <th>Tin u t</th>
            <th>Hnh thc</th>
            <th>Ni dung</th>
            <th>Ngy kch hot</th>
            <th>Trng thi</th>
            <th>hnh ng</th>
            </tr>
          </thead>
          <tbody>
            {tableList.map((item, index) => {
              return (
                <tr key={index}>
                  <td className="no-wrap">{item.contractCode}</td>
                  <td>{item.name}</td>
                  <td className="no-wrap">{item.investMoney}</td>
                  <td>{item.investType === 0 ? "Chuyn khon" : "Tin mt"}</td>
                  <td>{item.content}</td>
                  <td>{item.processDate}</td>
                  <td>{item.status === 0 ? "Cha x l" : "ang x l"}</td>
                  <td><button className={`process__btn ${item.status === 0 ? "" : "gray__btn"}`} type="button" onClick={() => showModalType(item)}>Kch hot</button></td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
        <BsModal
        className="process-modal"
        toggleModal={toggleModal}
        onClick={() => setToggleModal(false)}
      >
       { toggleModal ?
         <InvestProcessModal itemProcess={modalType} closeModal={() =>  setToggleModal(false)}/> : ""
       }
      </BsModal>
        <PaginationTable list={tableList} />
      </div>
  );
};

export default TableList;