const TableList = () => {
  const [toggleModal, setToggleModal] = useState(false);
  const [modalType, setModalType] = useState("");
  const showModalType = value => {
    setModalType(value);
    setToggleModal(true);
  };
  return (
      <div className="table-content">
        <div className="table">
          <table>
            <thead>
            <tr>
              <th>M H</th>
              <th>h v tn <br/>Nh u t</th>
              <th>Tin u t</th>
              <th>K hn</th>
              <th>Kch hot</th>
              <th>Ht k hn</th>
              <th>Hnh thc</th>
              <th>Ngi kch hot</th>
              <th>Trng thi</th>
              </tr>
            </thead>
            <tbody>
              {tableList.map((item, index) => {
                return (
                  <tr key={index}>
                    <td className="no-wrap"><button className="show__info" type="button" onClick={() => showModalType("contractCode")}>{item.contractCode}</button></td>
                    <td>{item.name}</td>
                    <td className="no-wrap">{item.investMoney}</td>
                    <td className="no-wrap">{item.period}</td>
                    <td>{item.dateActive}</td>
                    <td>{item.endate}</td>
                    <td>{item.investType === 0 ? <button className="show__info" type="button" onClick={() => showModalType(0)}>Chuyn khon</button> : <button className="show__info" type="button" onClick={() => showModalType(1)}>Tin mt</button>}</td>
                    <td>{item.activePerson}</td>
                    <td>{item.status === 1 ? "Ch kch hot" : "Kch hot"}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        <BsModal
        className={modalType === "contractCode" ? "contract-img" : modalType === 0 ? "transform-trade" : "cash-trade"}
        toggleModal={toggleModal}
        onClick={() => setToggleModal(false)}
      >
        {modalType === "contractCode" ? (
          <ContractImagesModal closeModal={()=>setToggleModal(false)}/>
        ) : modalType === 0 ? (
          <InfoTransferModal closeModal={()=>setToggleModal(false)}/>
        ) : modalType === 1 ? (
          <InfoCashModal closeModal={()=>setToggleModal(false)}/>
        ) : (
          ""
        )}
      </BsModal>
        <PaginationTable list={tableList} />
      </div>
  );
};

export default TableList;