import React, { Fragment, useState } from "react";
import TimePicker from "react-time-picker";
import DatePicker from "react-date-picker";

const BlockLoginModal = ({closeModal}) => {
  const [time, setTime] = useState("00:00");
  const [date, setDate] = useState(new Date());
  const [reasonMessenger, setReasonMessenger] = useState("");
  const [timeLimit, setTimeLimit] = useState(false);
  const [submit, setSubmit] = useState(false);
  const onTimeChange = time => {
    setTime(time);
  };
  const onDateChange = date => {
    setDate(date);
  };
  const onSubmit = () => {
      console.log(time, date, reasonMessenger,timeLimit);
      setSubmit(true);
  }
  const onFinish = () => {
    setSubmit(false);
    closeModal();
    
  }
  return (
    <div className="block-login">
      <div className="head">
        <p className="title">Chn ng nhp</p>
        <p className="desc">
        Bn ang chn ng nhp ti khon ca nh u t: Nguyn Vn A
      </p>
      </div>
      {
        !submit ? 
        <Fragment>
        <div className="content">
      
        <label htmlFor="">Chn ng nhp ti</label>
        <div className="bs-row row-xs-10">
          <div className="bs-col xs-30-10">
            <div className="item">
              <TimePicker onChange={onTimeChange} value={time} />
            </div>
          </div>
          <div className="bs-col xs-35-10">
            <div className="item">
              <DatePicker onChange={onDateChange} value={date} />
            </div>
          </div>
          <div className="bs-col xs-35-10">
            <div className="item">
              <label htmlFor="timeLimit" className={`radio__label ${timeLimit ? "active" : ""}`}>
                {" "}
                <input
                  type="checkbox"
                  name="timeLimit"
                  id="timeLimit"
                  className="item__radio"
                  onChange={e=> setTimeLimit(e.target.checked)}
                />
                V thi hn
              </label>
            </div>
          </div>
          <div className="bs-col xs-100-10">
            <div className="item">
              <textarea
                name="reasonMessenger"
                id=""
                cols="30"
                rows="10"
                class="item__textarea"
                onChange={e => setReasonMessenger(e.target.value)}
                placeholder="L do b chn.."
              ></textarea>
            </div>
          </div>
        </div>
        </div>
        <div className="bottom">
            <button className="bottom__btn green__btn" type="button" onClick={onSubmit}>Chn ng nhp</button>
            <button className="bottom__btn red__btn" type="button">hy</button>
        </div>
        </Fragment>: <Fragment>
        <div className="content">
            <p className="desc">Mt email thng bo  c gi ti nh u t  h bit c l do b chn ng nhp</p>
        </div>
        <div className="bottom">
            <button className="bottom__btn green__btn" type="button" onClick={onFinish}>OK</button>
        </div>
        </Fragment>
      }
      </div>
  );
};

export default BlockLoginModal;
