import React, { Fragment, useState } from "react";

const OnLoginModal = ({closeModal}) => {
  const [reasonMessenger, setReasonMessenger] = useState("");
  const [submit, setSubmit] = useState(false);
  
  const onSubmit = () => {
      console.log(reasonMessenger);
      setSubmit(true);
  }
  const onFinish = () => {
    setSubmit(false);
    closeModal();
    
  }
  return (
    <div className="block-login">
      <div className="head">
        <p className="title">M ng nhp</p>
        <p className="desc">
        Bn ang m ng nhp ti khon ca nh u t: Nguyn Vn A
      </p>
      </div>
      {
        !submit ? 
        <Fragment>
        <div className="content">
        <div className="bs-row row-xs-10">
          <div className="bs-col xs-100-10">
            <div className="item">
              <textarea
                name="reasonMessenger"
                id=""
                cols="30"
                rows="10"
                class="item__textarea"
                onChange={e => setReasonMessenger(e.target.value)}
                placeholder="L do m.."
              ></textarea>
            </div>
          </div>
        </div>
        </div>
        <div className="bottom">
            <button className="bottom__btn green__btn" type="button" onClick={onSubmit}>M ng nhp</button>
            <button className="bottom__btn red__btn" type="button">hy</button>
        </div>
        </Fragment>: <Fragment>
        <div className="content">
            <p className="desc">Mt email thng bo  c gi ti nh u t  h bit c l do ti khon c m</p>
        </div>
        <div className="bottom">
            <button className="bottom__btn green__btn" type="button" onClick={onFinish}>OK</button>
        </div>
        </Fragment>
      }
      </div>
  );
};

export default OnLoginModal;
